import axios from 'axios'
import router from '@/router'
import {showLoading, hideLoading} from "@/utils/loading";


const instanceAdmin = axios.create({
    baseURL: "http://192.168.50.252:8282",
    timeout: 1000 * 5,
    headers: {'Content-Type': 'application/x-www-form-urlencoded'} //: 'application/x-www-form-urlencoded'??这个type传输键值对， 因为axios 提交表单的时候不是Form Data ，所以不能通过request.getParameter("username")的方式获取参数值
});


// 添加请求拦截器
instanceAdmin.interceptors.request.use(function (config) {
    // 在发送请求之前做些什么
    // 后台需要前端传些什么？参数
/*    console.log('登录发送请求。。。')
    if (sessionStorage.getItem('token')) {
        config.headers['Authorization'] = sessionStorage.getItem('token');
    } // 将token从localStorage中取出并添加到请求头的Authorization字段上
    console.log('登录发送请求成功')
    console.log(config)
    console.log(config.headers)
    console.log(config.headers.Authorization)
    // 业务需求*/

    console.log('登录发送请求。。。')
    console.log('登录发送请求成功')
    console.log(config)
    console.log(config.headers)
    console.log(config.headers.Authorization)

    showLoading();

    return config;
}, function (error) {
    // 对请求错误做些什么
    return Promise.reject(error);
});

// 添加响应拦截器
instanceAdmin.interceptors.response.use(function (response) {
    // 对响应数据做点什么
    hideLoading();

    return response;
}, function (error) {
    // 对响应错误做点什么
    hideLoading();
    return Promise.reject(error);
});

export default instanceAdmin


