<template>
  <transition
      appear
      @before-enter="beforeEnter"
      @enter="enter">
    <div id="login-main">
      <div class="login-wrap-main">
        <el-form ref="deliveryForm" :model="deliveryForm" :rules="rules" class="login-box-main" size="medium">
          <h3 style="text-align: center;color: #e18383">请输入收货信息</h3>
          <el-divider><span class="iconfont icon-pangxie" style="font-size: 25px; "></span></el-divider>
          <el-form-item prop="username" class="form-item">
            <label>卡号</label>
            {{ $route.params.username }}
          </el-form-item>
          <el-form-item prop="consignee" class="form-item">
            <label>收货人</label>
            <el-input type="text" placeholder="请输入收货人" v-model="deliveryForm.consignee"/>
          </el-form-item>
          <el-form-item prop="cellphone" class="form-item">
            <label>手机号</label>
            <el-input type="text" placeholder="请输入11位手机号" v-model="deliveryForm.cellphone"/>
          </el-form-item>
          <el-form-item prop="address" class="form-item">
            <label>详细地址</label>
            <el-input type="text" placeholder="请输入详细地址" v-model="deliveryForm.address"/>
          </el-form-item>
          <span style="font-size: small; color: #3a6f92; user-select: none">Tips:为了保证时令品的新鲜度,收到订单后将即刻为您发货</span>
          <el-button type="danger" round class="login-btn block" @click="submitForm('deliveryForm')">确认提交</el-button>
        </el-form>
      </div>
    </div>
  </transition>
</template>

<script>
import {deliverySubmit} from '../api/login'
import {reactive, ref, isRef,} from '@vue/composition-api'
import gsap from "gsap";

export default {
  name: "Main",
  setup(props, context) {

    const {$message} = context.root
    const {$route} = context.root
    const {$router} = context.root
    const {$confirm} = context.root

    const beforeEnter = ((el) => {
      console.log("beforeEnter-- set initial state 设置初始化状态")
      el.style.transform = 'translateY(-60px)'
      el.style.opacity = 0
    })
    const enter = ((el, done) => {
      console.log(" starting to enter-- make transition 开始动画")
      gsap.to(el, {
        duration: 0.8,
        y: 0,
        opacity: 1,
        onComplete: done,
      })
    })

    let validateCellphone = (rule, value, callback) => {
      let regex = /^1(3[0-9]|4[01456879]|5[0-35-9]|6[2567]|7[0-8]|8[0-9]|9[0-35-9])\d{8}$/;
      if (value === '') {
        callback(new Error('请输入11位手机号码'));
      } else if (!regex.test(value)) {
        callback(new Error('请检查您的手机号码'));
      } else {
        callback();
      }
    };

    const deliveryForm = reactive({
      username: '',
      consignee: '',
      address: '',
      cellphone: ''
    })

    const rules = reactive({
      consignee: [{required: true, message: '请输入收货人名', trigger: 'blur'}],
      address: [{required: true, message: '请输入详细收货地址', trigger: 'blur'}],
      cellphone: [{required: true, validator: validateCellphone, trigger: 'blur'}],
    })

    const submitForm = ((formName) => {
      context.refs[formName].validate((valid) => {
        if (valid) {
          confirmSubmit();
        } else {
          console.log('error submit');
          return false;
        }
      })
    })

    /*const customerInfoSubmit = (() => {
      let requestData = {
        username: $route.params.username,
        consignee: deliveryForm.consignee,
        cellphone: deliveryForm.cellphone,
        address: deliveryForm.address
      }
      deliverySubmit(requestData).then((response) => {
        $message.success('提交成功');
        console.log(response);//取得map[username,consignee，cellphone，address,poNumber等等]
        $router.replace({
          name: 'Message', params: {
            poNumber: response.data.poNumber,
            username: response.data.username,
            consignee: response.data.consignee,
            cellphone: response.data.cellphone,
            address: response.data.address,
            orderStatus: response.data.orderStatus,
          }
        });
        console.log(requestData.consignee);
        console.log(requestData.username);
        console.log(response.data.poNumber);
        console.log(response.data.orderStatus);
      }).catch(error => {
        $message.error('请登录');
      })
    })*/


    const confirmSubmit = (() => {
      let requestData = {
        username: $route.params.username,
        consignee: deliveryForm.consignee,
        cellphone: deliveryForm.cellphone,
        address: deliveryForm.address
      }
      $confirm('您的收货信息为：\n' + requestData.consignee + '\n' + requestData.address + '\n' + requestData.cellphone,
          '确认收货信息', {
            confirmButtonText: '确认提交',
            cancelButtonText: '修改',
            dangerouslyUseHTMLString: true
          })
          .then(() => {
            let requestData = {
              username: $route.params.username,
              consignee: deliveryForm.consignee,
              cellphone: deliveryForm.cellphone,
              address: deliveryForm.address
            }
            deliverySubmit(requestData).then((response) => {
              $message.success('提交成功');
              console.log(response);//取得map[username,consignee，cellphone，address,poNumber等等]
              $router.replace({
                name: 'Message', params: {
                  poNumber: response.data.poNumber,
                  username: response.data.username,
                  consignee: response.data.consignee,
                  cellphone: response.data.cellphone,
                  address: response.data.address,
                  orderStatus: response.data.orderStatus,
                }
              });
              console.log(requestData.consignee);
              console.log(requestData.username);
              console.log(response.data.poNumber);
              console.log(response.data.orderStatus);
            }).catch(error => {
              $message.error('请登录');
            })
          }).catch(() => {
        $message.info('请修改信息')
      })
    })


    return {deliveryForm, rules, submitForm, confirmSubmit,beforeEnter,enter}


  }
}

</script>

<style lang="scss" >
#login-main {
  //background-color: #455d7b;
  //height: 100vh;

  display: flex;
  overflow-x: auto;
  flex: none;
  background: url("../assets/pic/xt1.png") no-repeat;
  /*  background-position: center;*/
  height: 100%;
  width: 100%;
  background-size: cover;
  /*  position: fixed;*/
}


.login-wrap-main {
  width: 460px;
  margin: auto;
}

.login-box-main {
  margin-top: 29px;
  margin-bottom: 570px;

  label {
    display: block;
    margin-bottom: 3px;
    font-size: 14px;
    color: #d1daed;
  }

  .form-item {
    margin-bottom: 13px;
    color: #a45f6e;
  }

  .block {
    width: 100%;
    display: block;
  }

  .login-btn {
    margin-top: 25px;
  }

}


.el-notification {
  white-space: pre-wrap !important;
}

.el-message-box {
  width: 350px;
}

.el-message-box__content {
  white-space: pre-wrap !important;
}

</style>
