<template>
  <transition
      appear
      @before-enter="beforeEnter"
      @enter="enter">
    <div><router-link :to="{name:'Pie'}" style="text-decoration: none"><el-button size="medium">饼图</el-button></router-link>
  <!-- 为ECharts准备一个具备大小（宽高）的Dom -->
  <div id="myChart" :style="{width:'1000px',height:'500px'}"></div>
  </div>
  </transition>
</template>

<script>
import gsap from "gsap";
const {barVo} = require("@/api/login");

export default {
  name: "Statistics",
  methods:{
    beforeEnter(el) {
      console.log("beforeEnter-- set initial state 设置初始化状态")
      el.style.transform = 'translateY(100px)'
      el.style.opacity = 0
    },

    enter(el, done) {
      console.log(" starting to enter-- make transition 开始动画")
      gsap.to(el, {
        duration: 2,
        y: 0,
        opacity: 1,
        onComplete: done,
      })
    }

  },

  mounted() {
    barVo().then((response) => {
      console.log(response)
      // 基于准备好的dom，初始化echarts实例
      //let _this = this
      let myChart = this.$echarts.init(document.getElementById("myChart"))
      // 指定图表的配置项和数据
      myChart.setOption({
        title: {
          text: '产品销量统计',
          left: 'center',
          top: 20,
          textStyle: {color: '#460b15'}
        },
        tooltip: {},
        xAxis: {
          data: response.data.names,
          axisLabel: {
            interval: 0,
            rotate: 15
          }
        },
        yAxis: {},
        series: [{
          name: '销量',
          type: 'bar',
          data: response.data.values
        }]

      });

    })

  }


}
</script>

