<template>
  <transition
      appear
      @before-enter="beforeEnter"
      @enter="enter">
    <div class="edit-wrap">
      <el-collapse-transition>
        <div v-show="show3">
          <el-form style="width: 600px" ref="form" :model="form" class="edit-box">
            <el-divider><span class="iconfont iconkehu3" style="font-size: 22px; "></span></el-divider>
            <h3>客户信息更新:</h3>
            <el-tooltip class="item" effect="dark" content="这里可根据订单号修改客户在填写提货信息时可能输入错误的信息(包括客户姓名、手机号、收货地址)" placement="top-start">
            <el-alert style="margin: 25px 0 10px 0"
                      title="更新客户具体信息,请谨慎修改."
                      type="info"
                      :closable="false"
                      effect="dark">
            </el-alert>
            </el-tooltip>
            <el-form-item class="form-item" v-show="false">
              <label>ID</label>
              <el-input v-model="form.id" readonly></el-input>
            </el-form-item>
            <el-form-item class="form-item">
              <label>订单号</label>
              <el-input v-model="form.purchaseNo" readonly style="width: 180px"
                        prefix-icon="el-icon-success"></el-input>
            </el-form-item>
            <el-form-item class="form-item">
              <label>客户名</label>
              <el-input v-model="form.consignee" style="width: 155px" prefix-icon="el-icon-user-solid"></el-input>
            </el-form-item>
            <el-form-item class="form-item">
              <label>手机</label>
              <el-input v-model="form.cellphone" style="width: 155px" prefix-icon="el-icon-phone"></el-input>
            </el-form-item>
            <el-form-item class="form-item">
              <label>收货地址</label>
              <el-input v-model="form.address" prefix-icon="el-icon-location"></el-input>
            </el-form-item>
            <el-form-item class="form-item" v-show="false">
              <label>商品型号</label>
              <el-input v-model="form.productType" readonly style="width: 200px"
                        prefix-icon="el-icon-s-goods"></el-input>
            </el-form-item>
            <el-form-item class="form-item" v-show="false">
              <label>承运人</label>
              <el-input v-model="form.trackingNo" autocomplete="off" placeholder="请输入快递单号" style="width: 400px">
                <el-select v-model="form.carrier" slot="prepend" placeholder="请选择快递类型" style="width: 120px">
                  <el-option label="EMS" value="EMS"></el-option>
                  <el-option label="顺丰速运" value="顺丰速运"></el-option>
                  <el-option label="申通快递" value="申通快递"></el-option>
                  <el-option label="圆通速递" value="圆通速递"></el-option>
                  <el-option label="中通快递" value="中通快递"></el-option>
                  <el-option label="百世快递" value="百世快递"></el-option>
                  <el-option label="韵达速递" value="韵达速递"></el-option>
                  <el-option label="京东快递" value="京东快递"></el-option>
                  <el-option label="宅急送" value="宅急送"></el-option>
                  <el-option label="其它" value="others"></el-option>
                </el-select>
              </el-input>
            </el-form-item>
            <el-form-item class="form-item">
              <label v-if="false">订单状态</label>
              <el-select v-model="form.orderStatus" placeholder="请修改订单状态" style="width: 180px" v-show="false">
                <el-option label="订单已发货" value="订单已发货"></el-option>
                <el-option label="订单已完成" value="订单已完成"></el-option>
                <el-option label="收到订单,等待处理" value="收到订单,等待处理"></el-option>
                <el-option label="退换货等" value="退换货等"></el-option>
              </el-select>
              <el-button type="danger" style="margin-top: 13px" @click="onSubmit('form')">提交修改</el-button>
              <el-button @click="cancel">取消</el-button>
            </el-form-item>
            <!--          <el-form-item class="form-item">
                        <label>运单号</label>
                        <el-input v-model="form.trackingNo" autocomplete="off" placeholder="请输入快递单号"></el-input>
                      </el-form-item>-->
            <!--          <el-form-item class="form-item">
                        <el-button type="primary" @click="onSubmit('form')">提交</el-button>
                        <el-button @click="cancel">取消</el-button>
                      </el-form-item>-->
          </el-form>
        </div>
      </el-collapse-transition>
    </div>
  </transition>
</template>


<script>

import {findById, updateById} from "@/api/login"
import gsap from "gsap";

export default {
  name: "EditCustomers",
  data() {
    return {
      form: {
        id: '',
        purchaseNo: '',
        consignee: '',
        cellphone: '',
        address: '',
        productType: '',
        carrier: '',
        trackingNo: '',
        orderStatus: '',
      },
      show3: true,
    }
  },
  created() {
    let id = this.$route.query.id
    let that = this
    console.log(id)
    findById(id).then((response) => {
      that.form = response.data
      console.log(response.data)
    })
  },

  methods: {
    onSubmit(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let these = this
          let customerInfoWithInfoChanged = this.form
          updateById(customerInfoWithInfoChanged).then((response) => {
            if (response.data) {
              this.show3 = false;
              setTimeout(() => {
                these.$alert('客户信息已更新. \n手机号:"' + these.form.cellphone + '"\n' + '客户地址:"' + these.form.address + '"\n'+'客户名:"'+these.form.consignee+'"', '更新客户信息', {
                  confirmButtonText: '确定', callback: () => {
                    these.$router.push('/customer/customerInfo')
                    this.open1()
                  }
                })
              }, 500)
            }
          })
        }
      })
    },

    cancel() {
      this.show3 = false;
      setTimeout(() => {
        this.$router.push('customer/customerInfo')
      }, 500);
      this.open2()
    },

    open1() {
      this.$notify({
        title: '更新成功',
        message: '客户信息已更新',
        type: 'success'
      });
    },

    open2() {
      this.$notify({
        title: '取消操作',
        message: '客户信息未变化',
        type: 'info'
      });
    },

    beforeEnter(el) {
      console.log("beforeEnter-- set initial state 设置初始化状态")
      el.style.transform = 'translateX(60px)'
      el.style.opacity = 0
    },

    enter(el, done) {
      console.log(" starting to enter-- make transition 开始动画")
      gsap.to(el, {
        duration: 1,
        x: 0,
        opacity: 1,
        onComplete: done,
      })
    }
  }
}
</script>

<style lang="scss" scoped>

.edit-wrap {
  width: 600px;
  margin: auto;
}

.edit-box {
  margin-top: 29px;

  label {
    display: block;
    margin-bottom: 3px;
    font-size: 14px;
    color: azure;
  }

  .form-item {
    margin-bottom: 13px;
  }

}

.el-notification {
  white-space: pre-wrap !important;
}

.el-message-box__content {
  white-space: pre-wrap !important;
}


</style>

