<template>
  <transition
      appear
      @before-enter="beforeEnter"
      @enter="enter">
    <div id="four0four">404<br>
      <p>页面没找到</p>
      <el-button type="danger" @click="$router.push('/')">返回</el-button>
    </div>
  </transition>
</template>

<script>
import gsap from "gsap";

export default {
  name: "NotFound404",
  setup() {
    const beforeEnter = ((el) => {
      console.log("beforeEnter-- set initial state 设置初始化状态")
      el.style.transform = 'translateY(-100px)'
      el.style.opacity = 0
    })
    const enter = ((el, done) => {
      console.log(" starting to enter-- make transition 开始动画")
      gsap.to(el, {
        duration: 1,
        y: 0,
        opacity: 1,
        onComplete: done,
        ease: 'bounce.out',
      })
    })


    return {beforeEnter, enter}
  }
}
</script>

<style scoped>
#four0four {
  width: 330px;
  margin: auto;
}

</style>
