<template>
  <div class="contact">
    <h2 style="color: #b39da3">联系我们</h2>
    <transition-group
        appear
        tag="ul"
        @before-enter="beforeEnter"
        @enter="enter">
      <li v-for="(icon,index) in icons" :key="icon.name" :data-index="index">
        <p :class="icon.name" ></p>
        <div style="margin-top: 3px">{{ icon.text }}</div>
      </li>
    </transition-group>
  </div>
</template>

<script>

import gsap from "gsap";
import {ref} from '@vue/composition-api'

export default {
  name: "Contact",
  setup() {
    const icons = ref([
      {name: 'iconfont iconziyuan', text: '电子邮件 2888666@qq.com'},
      {name: 'iconfont iconphone', text: '联系电话 13888888888'},
      {name: 'iconfont iconweixin3', text: '微信 wechat123'},
      {name: 'iconfont icondizhi', text: '欢迎莅临 江苏省苏州工业园区唯亭镇阳澄湖阳澄湖大道阳澄湖大道8888号'},
    ])

    const beforeEnter =(el)=>{
      el.style.opacity = 0;
      el.style.transform ='translateY(90px)'
    }

    const enter =(el,done)=>{
      gsap.to(el,{
        opacity:1,
        y:0,
        duration:0.8,
        onComplete:done,
        delay:el.dataset.index*0.2
      })
    }

    return{icons,beforeEnter,enter}

  }
}
</script>

<style lang="scss" scoped>

.contact{
  text-align: center;

}

p{
  margin-block-start: 0.5em;
  margin-block-end: 0.5em;
}

ul{
  padding-inline-start: 0;
}

li{
  display: inline-block;
  width: 43%;
  height: 150px;


  background-color: #a7b4b5;
  border-radius: 2px;

  margin:10px 10px 0 0 ;

}

</style>
