<template>
  <transition
      appear
      @before-enter="beforeEnter"
      @enter="enter">
  <el-table
      :data="data.tableData"
      style="width: 100% "
      :header-cell-style="headClass"
      :cell-style="rowClass"
      border
      :default-sort="{prop: 'updateTime', order: 'descending'}"
      :row-class-name="tableRowClassName">
    <el-table-column
        label="序号"
        type="index"
        width="50"
        align="center">
    </el-table-column>
    <el-table-column prop="id" label="ID" width="50" v-if="false">
    </el-table-column>
    <el-table-column prop="purchaseNo" label="订单号" width="155">
    </el-table-column>
    <el-table-column
        label="提交日期" prop="updateTime" width="145">
    </el-table-column>
    <el-table-column
        label="客户名" width="80">
      <template #default="scope">
        <el-popover effect="light" trigger="hover" placement="top">
          <template #default>
            <h3>客户信息：</h3>
            <hr>
            <p>卡号: {{ scope.row.username }}</p>
            <p>订单: {{scope.row.purchaseNo}}</p>
            <p>姓名: {{ scope.row.consignee }}</p>
            <p>地址: {{ scope.row.address }}</p>
            <p>电话: {{ scope.row.cellphone }}</p>
            <p>物流: {{scope.row.carrier}}{{ scope.row.trackingNo }}</p>
          </template>
          <template #reference>
            <div class="name-wrapper">
              <el-tag size="medium">{{ scope.row.consignee }}</el-tag>
            </div>
          </template>
        </el-popover>
      </template>
    </el-table-column>
    <el-table-column
        label="联系电话" prop="cellphone" width="115">
    </el-table-column>
    <el-table-column
        label="客户地址" prop="address" width="600">
    </el-table-column>
    <el-table-column
        label="承运人" prop="carrier" width="80">
    </el-table-column>
    <el-table-column
        label="运单号" prop="trackingNo" width="135">
    </el-table-column>
    <el-table-column
        label="商品型号" prop="productType" width="160">
    </el-table-column>
    <el-table-column
        label="订单状态" prop="orderStatus" width="140">
    </el-table-column>
    <el-table-column
        label="状态进度" width="100">
      <template #default="scope">
        <el-progress :text-inside="true" :stroke-width="20" :percentage=scope.row.progress
                     status="exception"></el-progress>
      </template>
    </el-table-column>
    <el-table-column label="订单操作">
      <template #default="scope">
        <el-button
            size="mini"
            type="primary" plain
            @click="findCustomer(scope.row)">处理
        </el-button>
        <el-button
            size="mini"
            type="info" plain
            @click="handleDelete(scope.row)" >删除
        </el-button>
      </template>
    </el-table-column>
  </el-table>
  </transition>
</template>

<script>

import {ref, onMounted, reactive, getCurrentInstance, computed} from "@vue/composition-api";
import {adminGetNewArrivalOrder} from "@/api/login";
import {deleteById} from "@/api/login";
import gsap from "gsap";

export default {
  name: "NewArrivalOrder",
  setup(props, context) {

    const data = reactive({
          tableData: [{
            id: '',
            purchaseNo: '',
            updateTime: '',
            username: '',
            consignee: '',
            cellphone: '',
            address: '',
            trackingNo: '',
            orderStatus: '',
            productType:'',
            progress: 0,
            carrier:''
          }]
        }
    )

    const beforeEnter = ((el) => {
      console.log("beforeEnter-- set initial state 设置初始化状态")
      el.style.transform = 'translateX(-60px)'
      el.style.opacity = 0
    })
    const enter = ((el, done) => {
      console.log(" starting to enter-- make transition 开始动画")
      gsap.to(el, {
        duration: 0.5,
        x: 0,
        opacity: 1,
        onComplete: done,
      })
    })

    const getNewOrder = (() => {
      adminGetNewArrivalOrder().then((response) => {
        console.log("刷新新订单");
        data.tableData = response.data;
        console.log(data.tableData);
      }).catch(() => {
      })
    })

    onMounted(() => {
      getNewOrder()
    })

    const tableRowClassName = (({row, rowIndex}) => {
      if (row.orderStatus === '收到订单,等待处理') {
        row.progress = 50;

        return 'warning-row-new';
      } else if (row.orderStatus === '订单已发货') {
        row.progress = 75;
        return 'success-row-new';
      } else if (row.orderStatus === '订单已完成') {
        row.progress = 100;
        return 'mission-accomplished-row-new';
      } else if (row.orderStatus === '退换货等') {
        row.progress = 25;
        return 'changing-or-refunding-row-new';
      }
      return '';
    })

    const {$message} = context.root
    const {$confirm} = context.root
    const {$alert} = context.root
    const {$prompt} = context.root
    const {$router} = context.root


    const findCustomer = ((row) => {
      $router.push('/edit?id=' + row.id)
      console.log(row);
    })


    const handleDelete = ((row) => {
      $confirm('您是否确定删除订单' + row.purchaseNo + '\n' + '此订单状态为：[' + row.orderStatus + ']\n' + '确认将清除此记录.', '温馨提示',{
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        reconfirm(row);
      }).catch(() => {
        $message.info('已取消操作')
      })

    })

    const reconfirm = ((row) => {
      $prompt('请输入[管理员秘钥]', '温馨提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消', type: 'warning'
      })
          .then(({value}) => {
            if ('123' === value) {
              deleteById(row.id).then((response) => {
                console.log("准备删除一条数据。。。。。。")
                if (response.data.code !== 2001 && response.data.code !== 3001) {
                  console.log("一条数据被成功删除")
                  $alert('成功删除一条数据', '删除数据', {
                    confirmButtonText: '好的',
                    callback: getNewOrder()
                  })
                }else{
                  $alert('权限不足,请联系管理员！', '删除数据', {
                    confirmButtonText: '好的'
                  })
                }
              })
            } else {
              $message.error('输入不正确,已取消操作')
            }
          })
          .catch(() => {
            $message.success('已取消可能的误操作')
          })

    })

    const headClass=(()=>{
      return 'text-align:center;background:#385166;'
    })

    const rowClass=(()=>{
      return 'text-align: center;'
    })

    return {
      data, findCustomer, handleDelete, getNewOrder, tableRowClassName,
      reconfirm,headClass,rowClass,beforeEnter,enter
    }

  }
}
</script>

<style lang="scss">
.el-table .warning-row-new {
  background: #e2c7c7;
  color: #2c2727;
}

.el-table .success-row-new {
  background: #788cb3;
  color: #2c2727;

}

.el-table .mission-accomplished-row-new {
  background: #97a88b;
}

.el-table .changing-or-refunding-row-new {
  background: #cb6c4e;
  color: #2c2727;
}
</style>
