<template>
  <transition
      appear
      @before-enter="beforeEnter"
      @enter="enter">
    <div>
      <div style="text-align: right;">

        <el-input type="text" v-model="searchValue" style="width: 200px" @keyup.enter.native="searchAction"
                  placeholder="输入搜索"/>

        <el-button size="mini" style="border-radius: 0" icon="el-icon-search" @click="searchAction">搜索</el-button>
        <el-button size="mini" style="border-radius: 0" class="iconfont iconpangxie2" @click="getOrder"> 全部
        </el-button>

        <el-button size="mini" style="border-radius: 0" class="iconfont iconpangxie2" @click="fileOutputConfirm"> 导出
        </el-button>
      </div>
      <el-table id="table"
                :data="data.tableData"
                style="width: 100%"
                :header-cell-style="headClass"
                :cell-style="rowClass"
                border
                :default-sort="{prop: 'updateTime', order: 'descending'}"
                :row-class-name="tableRowClassName">
        <el-table-column
            label="序号"
            type="index"
            width="50"
            align="center">
        </el-table-column>
        <el-table-column prop="id" label="ID" width="55" v-if="false">
        </el-table-column>
        <el-table-column prop="purchaseNo" label="订单号" width="155">
        </el-table-column>
        <el-table-column
            label="提交日期" prop="updateTime" width="145">
        </el-table-column>
        <el-table-column
            label="客户名" width="80">
          <template #default="scope">
            <el-popover effect="light" trigger="hover" placement="left">
              <template #default>
                <h3>客户信息：</h3>
                <hr>
                <p>卡号: {{ scope.row.username }}</p>
                <p>订单: {{ scope.row.purchaseNo }}</p>
                <p>姓名: {{ scope.row.consignee }}</p>
                <p>地址: {{ scope.row.address }}</p>
                <p>电话: {{ scope.row.cellphone }}</p>
                <p>物流: {{ scope.row.carrier }}{{ scope.row.trackingNo }}</p>
              </template>
              <template #reference>
                <div class="name-wrapper">
                  <el-tag size="medium">{{ scope.row.consignee }}</el-tag>
                </div>
              </template>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column
            label="联系电话" prop="cellphone" width="115">
        </el-table-column>
        <el-table-column
            label="客户地址" prop="address" width="580">
        </el-table-column>
        <el-table-column
            label="承运人" prop="carrier" width="80">
        </el-table-column>
        <el-table-column
            label="运单号" prop="trackingNo" width="140">
        </el-table-column>
        <el-table-column
            label="商品型号" prop="productType" width="185">
        </el-table-column>
        <el-table-column
            label="订单状态" prop="orderStatus" width="140">
        </el-table-column>
        <el-table-column
            label="状态进度" width="90">
          <template #default="scope">
            <el-progress :text-inside="true" :stroke-width="20" :percentage=scope.row.progress
                         :color=scope.row.progressColor></el-progress>
          </template>
        </el-table-column>
        <el-table-column label="订单操作" align="right">


          <template #default="scope">
            <el-button
                size="mini"
                type="primary" plain
                @click="findCustomer(scope.row)">处理
            </el-button>
            <el-button
                size="mini"
                type="info" plain
                @click="handleDelete(scope.row)">删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
          background
          layout="prev, pager, next"
          :page-size="pgSize"
          :currentPage="1"
          :total="total"
          @current-change="page">
      </el-pagination>
    </div>
  </transition>
</template>

<script>

import {onMounted, reactive, ref} from "@vue/composition-api";
import {adminGetOrder, adminGetOrderSearch, adminGetOrderNextPage} from "@/api/login";
import {deleteById} from "@/api/login";
import gsap from "gsap";
import XLSX from "xlsx";
import {Loading} from "element-ui";

export default {
  name: "Order",
  setup(props, context) {

    const openFullScreen2FileOutput = (() => {
      let loading = Loading.service({
        lock: true,
        text: '数据下载中...',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      setTimeout(() => {
        loading.close();
      }, 5000);
    })

    const fileOutputConfirm = (() => {
      $confirm('导出本页文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        exportToExcel();
        openFullScreen2FileOutput();
        setTimeout(() => {
          $message({
            type: 'success',
            message: '导出成功!'
          })
        }, 5200)
      }).catch(() => {
        $message({
          type: 'info',
          message: '已取消'
        });
      });
    })


    const exportToExcel = (() => {
      let table = document.getElementById('table');
      let workBook = XLSX.utils.table_to_book(table);
      try {
        XLSX.writeFile(workBook, 'yourFile.xlsx');
      } catch (e) {
        console.log(e);
      }
    })

    const searchValue = ref('')

    const searchAction = (() => {
      let requestData = {
        keyWords: searchValue.value
      }
      if (searchValue.value.trim() === '') {
        openMsg();
        return;
      }
      adminGetOrderSearch(requestData).then((response) => {
        data.tableData = response.data;
        total.value = response.data.length
        pgSize.value = response.data.length
        //console.log(response.data.length)
        console.log(response)
        //keyWordsShowColor(requestData.keyWords,response.data)
      }).catch(error => {
      })
    })

    const openMsg = (() => {
      $alert('<strong>请输入需要查找的信息...</strong>', '温馨提示', {
        confirmButtonText: '好的',
        dangerouslyUseHTMLString: true,
        type: 'warning',
        showClose: false,
        callback: action => {
          page(1)
        }

      });
    })

    /*    const keyWordsShowColor = ((key,array) => {
        })*/


    const data = reactive({
          tableData: [{
            id: '',
            purchaseNo: '',
            updateTime: '',
            username: '',
            consignee: '',
            cellphone: '',
            address: '',
            trackingNo: '',
            orderStatus: '',
            progress: 0,
            carrier: '',
            productType: '',
            progressColor: ''
          }],
        }
    )

    const total = ref(0)
    const pgSize = ref(0)

    const page = ((currentPage) => {
      let requestData = {
        currentPage: currentPage,
        pageSize: 30
      }
      adminGetOrderNextPage(requestData).then((response) => {
        console.log(response)
        data.tableData = response.data.records;
        total.value = response.data.total;
        pgSize.value = requestData.pageSize;
      }).catch((err) => {
      })
    })

    const beforeEnter = ((el) => {
      console.log("beforeEnter-- set initial state 设置初始化状态")
      el.style.transform = 'translateY(-60px)'
      el.style.opacity = 0
    })
    const enter = ((el, done) => {
      console.log(" starting to enter-- make transition 开始动画")
      gsap.to(el, {
        duration: 0.7,
        y: 0,
        opacity: 1,
        onComplete: done,
      })
    })

    const getOrder = (() => {
      adminGetOrder().then((response) => {
        console.log("刷新");
        data.tableData = response.data;
        total.value = response.data.length
        pgSize.value = response.data.length
        console.log(data.tableData);
        console.log(response)
      }).catch(() => {
      })
    })

    onMounted(() => {
      page(1);

    })

    const tableRowClassName = (({row, rowIndex}) => {
      if (row.orderStatus === '收到订单,等待处理') {
        row.progress = 50;
        row.progressColor = '#c05656'
        return 'warning-row';
      } else if (row.orderStatus === '订单已发货') {
        row.progress = 75;
        row.progressColor = '#7188b0';
        return 'success-row';
      } else if (row.orderStatus === '订单已完成') {
        row.progress = 100;
        row.progressColor = '#81967b'
        return 'mission-accomplished-row';
      } else if (row.orderStatus === '退换货等') {
        row.progress = 35;
        row.progressColor = '#da3b06';
        return 'changing-or-refunding-row';
      }
      return '';
    })

    const headClass = (() => {
      return 'text-align:center;background:#385166;'
    })

    const rowClass = (() => {
      return 'padding:1px'

    })


    /*    const hint = (({row, rowIndex}) => {
          switch (row.orderStatus) {
            case '收到订单,等待处理': {
              data.tableData.progress =50
              break;
            }
            case '订单已发货': {
              data.tableData.progress = 75
              break;
            }
            case '订单已完成': {
              data.tableData.progress = 100
              break;
            }
            case '退换货等': {
              data.tableData.progress = 25
            }
          }
        })*/


    const {$message} = context.root
    const {$confirm} = context.root
    const {$alert} = context.root
    const {$prompt} = context.root
    const {$router} = context.root


    const findCustomer = ((row) => {
      $router.push('/edit?id=' + row.id)
      console.log(row);
    })


    const handleDelete = ((row) => {
      $confirm('您是否确定删除订单' + row.purchaseNo + '\n' + '此订单状态为：[' + row.orderStatus + ']\n' + '确认将清除此记录.', '温馨提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        reconfirm(row);
      }).catch(() => {
        $message.info('已取消操作')
      })

    })

    const reconfirm = ((row) => {
      $prompt('请输入[管理员秘钥]', '温馨提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消', type: 'warning'
      })
          .then(({value}) => {
            if ('123' === value) {
              deleteById(row.id).then((response) => {
                console.log("准备删除一条数据。。。。。。")
                if (response.data.code !== 2001 && response.data.code !== 3001) {
                  console.log("一条数据被成功删除")
                  $alert('成功删除一条数据', '删除数据', {
                    confirmButtonText: '好的',
                    callback: page(1)
                  })
                }else{
                  $alert('权限不足,请联系管理员！', '删除数据', {
                    confirmButtonText: '好的'
                  })
                }
              })
            } else {
              $message.error('输入不正确,已取消操作')
            }
          })
          .catch(() => {
            $message.success('已取消可能的误操作')
          })

    })


    return {
      data,
      findCustomer,
      handleDelete,
      getOrder,
      tableRowClassName,
      reconfirm,
      headClass,
      rowClass,
      beforeEnter,
      enter,
      page,
      total,
      searchAction,
      searchValue,
      pgSize,
      openMsg,
      exportToExcel,
      fileOutputConfirm,
      openFullScreen2FileOutput
      //keyWordsShowColor
    }

  }
}

</script>

<style lang="scss">

div > .el-message-box {
  vertical-align: 22vh;

}

div > .el-input > input {
  height: 27px;
  border-radius: 0;
}


.el-table .warning-row {
  background: #dfbcbc;
  color: #ba4747;

}


.el-table .success-row {
  background: #7188b0;
  color: #4d4545;


}


.el-table .mission-accomplished-row {
  background: #859d7e;
  color: #4d4545;

}


.el-table .changing-or-refunding-row {
  background: #bb725a;
  color: #1e1e1e;
}

</style>


