<template>
  <el-container>
    <el-header style="text-align: left; font-size: 8px">
      <el-dropdown>
        <i class="el-icon-coffee" style="color:#59a06d; margin: 10px 5px 10px 1px; font-size: 25px"></i>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>
            <router-link :to="{name:'AdministratorModify'}" style="text-decoration: none">修改</router-link>
          </el-dropdown-item>
          <el-dropdown-item>
            <router-link :to="{name:'AdministratorNewAnAccount'}" style="text-decoration: none">新增</router-link>
          </el-dropdown-item>
          <el-dropdown-item @click.native="logout">退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <span
          style="font-size: small;color: #c8c4c0">{{ $store.getters.getUser.role }}-{{ $store.getters.getUser.username }}</span>
    </el-header>
    <el-container>
      <el-aside width="200px">
        <el-menu router
                 :unique-opened=false
                 >
<!--        <el-menu router :default-openeds="['8']"
                 >-->
          <el-submenu v-for="(item,index) in $router.options.routes" :key="item.id" :index="index + ''"
                      v-if="item.displayInMenu">
            <template slot="title"><span :class=item.icon style="margin-right: 5px;font-size: 15px;"></span>{{item.name }}
            </template>
            <el-menu-item v-for="(item2,index2) in item.children" :key="item2.id" :index="item2.path"
                          :class="$route.path===item2.path?'is-active':''">
              {{ item2.meta.title }}
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>

const {adminLogout} = require("@/api/login");
export default {
  name: "Index",
  /*methods: {
    setName(name) {
      window.localStorage.setItem('name', name)
    },
  },
  mounted() {
    this.setName('abc')
  }*/
  methods: {
    logout() {
      adminLogout().then((response) => {
        sessionStorage.clear();
        this.$store.commit('resetState');
        this.$router.push('/admin/administratorLogin')
        console.log(response)
      })
    }
  }
}
</script>

<style lang="scss">

.el-header {
  background-color: #30323a;
  color: #070101;
  text-align: center;

}

.el-aside {
  background-color: #475566;
  color: #333;
  text-align: center;
  line-height: 100px;
}

.el-main {
  background-color: #E9EEF3;
  color: #333;
  text-align: center;

}

.el-menu-item {
  background-color: #a7b0ba
}

.el-menu-item.is-active {
  background-color: #576372 !important;
  color: #c1c5d6;

}

.el-submenu__title {
  background-color: #313c50;
  color: #7e9ca5;

}


</style>
