<template>
  <el-container>
    <el-header style="text-align: right" >
      <span style="margin-right: 10px;color: #b99d9d" class="iconfont icon-pangxie"
                                               @click="$router.push('/admin/AdministratorLogin')">

      </span>
    </el-header>
    <el-main>
      <span style="font-size: smaller;color: #1b1c1b" >即刻提货，享受免费送货、新鲜保证、心意送达。</span>
      <div class="block">
        <el-carousel height="375px" indicatorPosition="outside" ref="carousel" :interval="4000">
          <el-carousel-item v-for="item in imageList" :key="item.id">
            <img :src="item.src" alt="item.alt"/>
          </el-carousel-item>
        </el-carousel>
      </div>
      <div>
          <el-row>
            <transition-group
                tag="el-col"
                appear
                @before-enter="beforeEnter"
                @enter="enter">
            <el-col :span="14" :key=1>
              <el-button class="pickUpBtn1" @click="$router.push('/login')"><span class="iconfont iconsonghuo1 "></span><h3>提货入口</h3></el-button>
            </el-col>
            <el-col :span="10" :key=2>
              <el-button class="pickUpBtn2" @click="$router.push('/contact')"><span class="iconfont iconweixin3"></span>Contact us</el-button>
            </el-col>
            </transition-group>
          </el-row>
      </div>


        <transition
            appear
            @before-enter="beforeEnter"
            @enter="enter"
            @after-enter="afterEnter"

            @before-leave="beforeLeave"
            @leave="leave"
            @after-leave="afterLeave">
          <p style="color: #b9b2b2">欢迎提货，祝您生活愉快。</p>
        </transition>

      <div>
        <el-row>
          <el-col :span="12"></el-col>
          <el-col :span="12"></el-col>

        </el-row>

      </div>
      <img src='@/assets/pic/hu1.png'>
      <img src="@/assets/pic/px1.png">

    </el-main>
    <el-footer><span class="iconfont iconpangxie2" style="margin-right: 5px;color: #a5a2a2"></span><span
        style="font-size: small;color: #a5a2a2">Copyright © 2021 Jaykucn. All Rights Reserved.</span></el-footer>
  </el-container>
</template>

<script>
import {reactive, ref} from '@vue/composition-api'
import gsap from 'gsap'

export default {
  name: "Home",
  setup() {
       const show = ref(true)

    const pictureOne= ref('@/assets/pic/hu1.png')

    const imageList= reactive([
      {id:0,src:require('@/assets/pic/app0210506115313.png'),alt:'pic1'},
      {id:1,src:require('@/assets/pic/sz20210505225202.jpg'),alt: 'pic2'},
      {id:2,src:require('@/assets/pic/12.jpg'),alt: 'pic3'},
      {id:3,src:require('@/assets/pic/34.jpg'),alt: 'pic4'},
      {id:4,src:require('@/assets/pic/56.jpg'),alt: 'pic5'},
    ])

    const beforeEnter = ((el) => {
      console.log("beforeEnter-- set initial state 设置初始化状态")
      el.style.transform = 'translateY(60px)'
      el.style.opacity = 0
    })
    const enter = ((el, done) => {
      console.log(" starting to enter-- make transition 开始动画")
      gsap.to(el, {
        duration: 1,
        y: 0,
        opacity: 1,
        ease: 'bounce.out',
        onComplete: done,
      })
    })

    const afterEnter = ((el,done) => {
      console.log("afterEnter...")
      gsap.to(el, {
        duration: 2,
        x: -439,
        onComplete:done,
      })
    })

    const beforeLeave = ((el,done) => {
      console.log("beforeLeave...")
    })

    const leave = ((el,done) => {
      console.log("leave")
    })

    const afterLeave = ((el,done) => {
      console.log("afterLeave", el)
    })

    return {imageList,beforeEnter, enter, afterEnter, beforeLeave, leave, afterLeave,pictureOne}
  }
}

</script>

