<template>
  <div id="login4Admin">
    <transition
    appear
    @before-enter="beforeEnter"
    @enter="enter">
    <div class="login-wrap-admin">
      <p style="color: #242222"><span class="iconfont icondenglu1"></span>后台管理</p>
      <el-form ref="loginForm" :model="loginForm" :rules="rules" class="login-box" size="medium">

        <el-form-item prop="username" class="form-item">
          <label><span class="iconfont iconlogin" style="font-size: 23px"></span></label>
          <el-input type="text" placeholder="请输入账号"
                    v-model="loginForm.username"/>
        </el-form-item>
        <el-form-item prop="password" class="form-item">
          <label><span class="iconfont iconmima10" style="font-size: 20px"></span></label>
          <el-input type="password" placeholder="请输入密码" show-password v-model="loginForm.password"
          @keyup.enter.native="submitForm('loginForm')"/>
        </el-form-item>
        <el-form-item>
          <el-button type="danger" class="login-btn block" @click="submitForm('loginForm')">
            登录
          </el-button>
        </el-form-item>
      </el-form>
    </div>
    </transition>
  </div>
</template>

<script>


import {reactive, ref, isRef, toRefs, onMounted,} from '@vue/composition-api'
import {adminLogin} from "@/api/login";
import gsap from "gsap";

export default {
  name: "AdministratorLogin",
  setup(props, context) {

    const {$message} = context.root //类似的:  const { $store, $message, $router, $route } = context.root
    const {$router} = context.root
    const {$store} = context.root

    const beforeEnter =(el)=>{
      el.style.opacity = 0;
      el.style.transform ='translateY(150px)'
    }

    const enter =(el,done)=>{
      gsap.to(el,{
        opacity:1,
        y:0,
        duration:1,
        onComplete:done
      })
    }


    let validateUsername = (rule, value, callback) => {
      let regex = /^[0-9a-zA-Z]{5,15}$/;
      if (value === '') {
        callback(new Error('请输入您的账号'));
      } else if (!regex.test(value)) {
        callback(new Error('输入的账号格式有误,需5-15位数字字母'));
      } else {
        callback();
      }
    };

    let validatePassword = (rule, value, callback) => {
      let regex = /^[0-9a-zA-Z]{5,25}$/;
      if (value === '') {
        callback(new Error('请输入密码'));
      } else if (!regex.test(value)) {
        callback(new Error('请检查您的密码,需5到25位数字字母'));
      } else {
        callback();
      }
    };


    const loginForm = reactive({
      username: '',
      password: '',
    })

    const rules = reactive({
      username: [
        {validator: validateUsername, trigger: 'blur'}],
      password: [
        {validator: validatePassword, trigger: 'blur'}],
    })

    const submitForm = ((formName) => {
      context.refs[formName].validate((valid) => {
        if (valid) {
          login();
        } else {
          console.log('error submit');
          return false;
        }
      })
    })

    const login = (() => {
      let requestData = {
        username: loginForm.username,
        password: loginForm.password,
      }
      adminLogin(requestData).then((response) => {
        if (response.data.data.role==='ROLE_superAdmin'||response.data.data.role==='ROLE_admin') {
          //const jwt = response.headers['authorization'];
          //sessionStorage.setItem('jwt', jwt);
          response.data.data.role=(response.data.data.role==='ROLE_superAdmin')?'超级管理员':'管理员';
          //登录成功后，vuex存储用户状态
          $store.dispatch('asyncUpdateUser', {username: response.data.data.username,role: response.data.data.role});
          console.log('管理员登录结果');
          console.log(response.data);
          console.log(response);
          $message.success("欢迎回来，"+ response.data.data.username);
          $router.push('/admin/index'/*{name: 'Order', params: {username: response.data.username,role: response.data.role}}*/);
        } else {
          $message.error("账号或密码不正确,请检查");
          console.log(response.data);
        }
      }).catch(error => {
      })
    })

    /*   const register = (() => {
         let requestData = {
           username: loginForm.username,
           password: loginForm.password
         }
         adminRegister(requestData).then((response) => {
           console.log('注册成功');
           console.log(response.data);
           $message.success("恭喜你,注册成功");
           context.refs['loginForm'].resetFields();
           $router.go(0);
         }).catch(error => {
         })
       })*/


    return {
      loginForm,
      rules,
      submitForm,
      login,
      beforeEnter,
      enter
    }

  }

}
</script>


<!--/*1、用户输入用户名和密码点击登入，向后端发送相关数据。

2、等待后端返回成功时，触发 store 中的 login() 方法，把 loginForm 对象传递给 store 中的 user 对象。

3、获取登录前页面的路径并跳转，如果该路径不存在，则跳转到首页
login() {
var _this = this
console.log(this.$store.state)
this.$axios
.post('/login', {
username: this.loginForm.username,
password: this.loginForm.password
})
.then(successResponse => {
if (successResponse.data.code === 200) {
// store 值存储
_this.$store.commit('login', _this.loginForm)
var path = this.$route.query.redirect
this.$router.replace({path: path === '/' || path === undefined ? '/index' : path})
}
})
.catch(failResponse => {
})
}
}
}*/&ndash;&gt;-->


<style lang="scss" scoped>
#login4Admin {
  height: 100vh;
  display: flex;
  overflow-x: auto;
  flex: none;
  background: url("../../assets/pic/xt2.png") no-repeat;
  /*  background-position: center;*/
  width: 100%;
  background-size: cover;
  position: fixed;
}



.el-divider__text{
  background: rgba(0,0,0,0);
}


.login-wrap-admin {
  width: 335px;
  margin: auto;
}

.menu-tab {
  text-align: center;

  li {
    display: inline-block;
    width: 88px;
    line-height: 36px;
    font-size: 14px;
    color: azure;
    border-radius: 2px;
    cursor: pointer;
  }

  .current {
    background-color: #2C3A47;
  }
}


.login-box {
  //margin-top: 5px;
  margin-bottom: 15px;

  label {
    display: block;
    margin-bottom: 3px;
    font-size: 14px;
    color: #c6a49b;
  }

  .form-item {
    margin-bottom: 13px;
  }

  .block {
    width: 100%;
    display: block;
  }

  .login-btn {
    margin-top: 15px;
  }
}


</style>

