const user = {          //获取的时候要将之前存储的JSON字符串使用JSON.parse()先转成JSON对象再进行操作
    state: sessionStorage.getItem('userState') ? JSON.parse(sessionStorage.getItem('userState')) : {
        user: {
            username: '',
            role: ''
        }
    },

    getters: {
        getUser(state){
            return state.user;
        }
    },

    mutations: {
        updateUser(state, user){
            state.user = user;
        },

        resetState(state){
            state.user = {}
        }
    },

    actions: {
        asyncUpdateUser(context,user){
            context.commit('updateUser',user)
        }
    }
};




export default user;
