<template>
  <transition
      appear
      @before-enter="beforeEnter"
      @enter="enter">
    <el-table
        :data="data.tableData"
        style="width: 100% "
        :header-cell-style="headClass"
        border
        :default-sort="{prop: 'updateTime', order: 'descending'}"
        :row-class-name="tableRowClassName">
      <el-table-column
          label="序号"
          type="index"
          width="50"
          align="center">
      </el-table-column>
      <el-table-column prop="id" label="ID" width="50" v-if="false">
      </el-table-column>
      <el-table-column prop="purchaseNo" label="订单号" width="160">
      </el-table-column>
      <el-table-column
          label="提交日期" prop="updateTime" width="150">
      </el-table-column>
      <el-table-column
          label="客户名" prop="consignee" width="130">
      </el-table-column>
      <el-table-column
          label="联系电话" prop="cellphone" width="115">
      </el-table-column>
      <el-table-column
          label="客户地址" prop="address" width="800">
      </el-table-column>
      <el-table-column
          label="承运人" prop="carrier" width="80" v-if="false">
      </el-table-column>
      <el-table-column
          label="运单号" prop="trackingNo" width="135" v-if="false">
      </el-table-column>
      <el-table-column
          label="商品型号" prop="productType" width="160" v-if="false">
      </el-table-column>
      <el-table-column
          label="订单状态" prop="orderStatus" width="140" v-if="false">
      </el-table-column>
      <el-table-column
          label="状态进度" width="100" v-if="false">
        <template #default="scope">
          <el-progress :text-inside="true" :stroke-width="20" :percentage=scope.row.progress
                       :color=scope.row.progressColor></el-progress>
        </template>
      </el-table-column>
      <el-table-column label="客户信息操作">
        <template #default="scope">
          <el-button
              size="mini"
              type="danger" plain
              @click="findCustomer(scope.row)">修改
          </el-button>
          <el-button v-if="false"
                     size="mini"
                     type="info" plain
                     @click="handleDelete(scope.row)">删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </transition>
</template>

<script>

import {onMounted, reactive} from "@vue/composition-api";
import {adminGetOrder} from "@/api/login";
import {deleteById} from "@/api/login";
import gsap from "gsap";

export default {
  name: "Order",
  setup(props, context) {

    const data = reactive({
          tableData: [{
            id: '',
            purchaseNo: '',
            updateTime: '',
            username: '',
            consignee: '',
            cellphone: '',
            address: '',
            trackingNo: '',
            orderStatus: '',
            progress: 0,
            carrier: '',
            productType: '',
            progressColor: ''
          }]
        }
    )

    const beforeEnter = ((el) => {
      console.log("beforeEnter-- set initial state 设置初始化状态")
      el.style.transform = 'translateX(100px)'
      el.style.opacity = 0
    })
    const enter = ((el, done) => {
      console.log(" starting to enter-- make transition 开始动画")
      gsap.to(el, {
        duration: 2,
        x: 0,
        opacity: 1,
        onComplete: done,
      })
    })

    const getOrder = (() => {
      adminGetOrder().then((response) => {
        console.log("刷新");
        data.tableData = response.data;
        console.log(data.tableData);
      }).catch(() => {
      })
    })

    onMounted(() => {
      getOrder();

    })

    const tableRowClassName = (({row, rowIndex}) => {
      if (row.orderStatus === '收到订单,等待处理') {
        row.progress = 50;
        row.progressColor = '#c05656'
        return 'warning-row';
      } else if (row.orderStatus === '订单已发货') {
        row.progress = 75;
        row.progressColor = '#6893b0';
        return 'success-row';
      } else if (row.orderStatus === '订单已完成') {
        row.progress = 100;
        row.progressColor = '#81967b'
        return 'mission-accomplished-row';
      } else if (row.orderStatus === '退换货等') {
        row.progress = 35;
        row.progressColor = '#da3b06';
        return 'changing-or-refunding-row';
      }
      return '';
    })





    const {$message} = context.root
    const {$confirm} = context.root
    const {$alert} = context.root
    const {$prompt} = context.root
    const {$router} = context.root


    const findCustomer = ((row) => {
      $router.push('/editAll?id=' + row.id)
      console.log(row);
    })


    const handleDelete = ((row) => {
      $confirm('注意：是否确定删除订单' + '[\n' + row.purchaseNo + '\n]' + '，此订单状态为：[\n' + row.orderStatus + '\n]' + '，确认将完全清除此记录及相关数据，且无法恢复。', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        reconfirm(row);
      }).catch(() => {
        $message.info('已取消操作')
      })

    })

    const reconfirm = ((row) => {
      $prompt('请输入[管理员秘钥]', '温馨提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消', type: 'warning'
      })
          .then(({value}) => {
            if (value === '123') {
              deleteById(row.id).then((response) => {
                if (response.data) {
                  $alert('删除成功', '删除数据', {
                    confirmButtonText: '好的',
                    callback: getOrder()
                  })
                }
              })
            } else {
              $message.error('输入不正确,已取消操作')
            }
          })
          .catch(() => {
            $message.success('已取消可能的误操作')
          })

    })

    const headClass = (() => {
      return 'text-align:center;background:#313C50;'
    })


    return {
      data, findCustomer, handleDelete, getOrder, tableRowClassName,
      reconfirm, headClass,beforeEnter,enter
    }

  }
}

</script>

<style lang="scss">


.el-table .warning-row {
  background: #dfb0b0;
  color: #ba4747;

}


.el-table .success-row {
  background: #8fa6b7;
  color: #4d4545;


}


.el-table .mission-accomplished-row {
  background: #8fa588;
  color: #4d4545;

}


.el-table .changing-or-refunding-row {
  background: #bb725a;
  color: #1e1e1e;
}

</style>


