<template>
  <div id="login">
    <transition
        appear
        @before-enter="beforeEnter"
        @enter="enter">
    <div class="login-wrap">
      <ul class="menu-tab">
        <li :class="{'current':item.status}" v-for="item in menuTab" :key="item.id" @click="toggleMenu(item)">
          {{ item.topic }}
        </li>
      </ul>
      <el-form ref="loginForm" :model="loginForm" :rules="rules" class="login-box" size="medium">
        <el-divider><i class="el-icon-sell" style="font-size: 25px;"></i></el-divider>
        <!--        <input type="hidden" name="_csrf" value="${_csrf.token}"></input>-->
        <el-form-item prop="username" class="form-item">
          <label>卡号</label>
          <el-input type="text" auto-complete="off" placeholder="请输入卡号"
                    v-model="loginForm.username" @input="v=>{loginForm.username=v.toUpperCase()}"/>
        </el-form-item>
        <el-form-item prop="password" class="form-item" v-if="menuTab[0].status">
          <label>卡密</label>
          <el-input type="password" auto-complete="off" placeholder="请输入卡密" show-password v-model="loginForm.password"/>
        </el-form-item>
        <el-form-item prop="cellphone" class="form-item" v-if="menuTab[1].status">
          <label>手机号</label>
          <el-input type="text" auto-complete="off" placeholder="请输入手机号" maxLength="16" v-model="loginForm.cellphone"
                    @keyup.enter.native="submitForm('loginForm')"/>
        </el-form-item>
        <el-form-item prop="code" class="form-item" v-if="menuTab[0].status">
          <label>验证码</label>
          <el-row :gutter="1">
            <el-col :span="12">
              <el-input type="text" auto-complete="off" minLength="1" maxLength="3" placeholder="请输入结果"
                        v-model="loginForm.code" @keyup.enter.native="submitForm('loginForm')"/>
            </el-col>
            <el-col :span="6">
              <div style="color: antiquewhite;text-align: center">{{ question1 }}+{{ question2 }}=?</div>
            </el-col>
            <el-col :span="6">
              <el-button type="primary" class="block" @click="toGetNum">刷新</el-button>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item>
          <el-button type="danger" class="login-btn block" @click="submitForm('loginForm')">
            {{ models === "login" ? "登录验证" : "查询信息" }}
          </el-button>
        </el-form-item>
      </el-form>
    </div>
    </transition>
  </div>
</template>

<script>
import {Message} from 'element-ui'
import {myLogin, userCheck} from '@/api/login'
import {reactive, ref, isRef, toRefs, onMounted,} from '@vue/composition-api'
import gsap from "gsap";

export default {
  name: "Login",
  setup(props, context) {

    const {$message} = context.root //类似的:  const { $store, $message, $router, $route } = context.root
    const {$router} = context.root

    const beforeEnter =(el)=>{
      el.style.opacity = 0;
      el.style.transform ='translateY(-90px)'
    }

    const enter =(el,done)=>{
      gsap.to(el,{
        opacity:1,
        y:0,
        duration:1,
        onComplete:done
      })
    }


    let validateUsername = (rule, value, callback) => {
      let regex = /^[0-9a-zA-Z]{5,15}$/;
      if (value === '') {
        callback(new Error('请输入您的卡号'));
      } else if (!regex.test(value)) {
        callback(new Error('输入的卡号格式有误'));
      } else {
        callback();
      }
    };

    let validatePassword = (rule, value, callback) => {
      let regex = /^[0-9a-zA-Z]{5,10}$/;
      if (value === '') {
        callback(new Error('请输入密码'));
      } else if (!regex.test(value)) {
        callback(new Error('请检查您的密码'));
      } else {
        callback();
      }
    };

    let validateCellphone = (rule, value, callback) => {
      let regex = /^1(3[0-9]|4[01456879]|5[0-35-9]|6[2567]|7[0-8]|8[0-9]|9[0-35-9])\d{8}$/;
      if (value === '') {
        callback(new Error('请输入手机号码'));
      } else if (!regex.test(value)) {
        callback(new Error('请检查您的手机号码'));
      } else {
        callback();
      }
    };

    let validateCode = (rule, value, callback) => {
      let regex = /^[0-9a-z]{1,3}$/;
      if (value === '') {
        callback(new Error('验证码不能为空'));
      } else if (!regex.test(value)) {
        callback(new Error('验证码格式有误'));
      } else if (value != question1.value + question2.value) {
        callback(new Error('请再算下哦'));
      } else {
        callback();
      }
    };

    const loginForm = reactive({
      username: '',
      password: '',
      cellphone: '',
      code: ''
    })

    const rules = reactive({
      username: [
        {validator: validateUsername, trigger: 'blur'}],
      password: [
        {validator: validatePassword, trigger: 'blur'}],
      cellphone: [
        {validator: validateCellphone, trigger: 'blur'}],
      code: [
        {validator: validateCode, trigger: 'blur'}]
    })

    const menuTab = reactive([
      {topic: '登录', status: true, type: 'login'},
      {topic: '查询', status: false, type: 'check'}
    ])

    const models = ref('login')

    const toggleMenu = ((item) => {
      menuTab.forEach((elem, index) => {
        elem.status = false
      });
      item.status = true;
      models.value = item.type;
      //console.log(models.value)
      context.refs['loginForm'].resetFields();
    })

    const question1 = ref(Math.round(Math.random() * 10))
    const question2 = ref(Math.round(Math.random() * 10))


    const toGetNum = (() => {
      if (loginForm.username == '') {
        $message.error('卡号不能为空');
        return false;
      }
      question1.value = Math.round(Math.random() * 10);
      question2.value = Math.round(Math.random() * 10)
    })

    const submitForm = ((formName) => {
      //console.log(models.value)
      context.refs[formName].validate((valid) => {
        if (valid) {
          if (models.value === 'login') {
            login();
          } else {
            check();
          }
        } else {
          console.log('error submit');
          return false;
        }
      })
    })

    const login = (() => {
      let requestData = {
        username: loginForm.username,
        password: loginForm.password,
        //code: loginForm.code
      }
      myLogin(requestData).then((response) => {
        if (response.data.code === 200 && response.data.data.valid === '有效' && response.data.data.enabled == true && response.data.data.role == 'ROLE_user') {
          //const jwt = response.headers['authorization'];
          //sessionStorage.setItem('jwt', jwt); //登录成功取得token放入sessionStorage
          console.log('登录成功，返回数据')
          console.log(response.data);
          console.log(response.headers);
          console.log(response);

          $message.success("恭喜你,验证登录成功");
          $router.push({name: 'Main', params: {username: requestData.username}});
        } else if (response.data.code === 200 && response.data.data.valid === '已使用') {
          $message.error("已使用，请前往查询页面查询");//路由跳转到查询选项的页面
          console.log(response.data);
        } else if (response.data.code === 2005) {
          $message.error("卡未激活，请联系管理员")
        } else {
          $message.error("卡号或卡密不正确,请检查");
          console.log(response.data);
        }
      }).catch(error => {
      })
    })

    const check = (() => {
      let requestData = {
        username: loginForm.username,
        cellphone: loginForm.cellphone   //这里的手机号再看看其它具体情况
      }
      userCheck(requestData).then((response) => {
        if (response.data.valid === '已使用') {
          console.log('登录结果');
          console.log(response.data);
          $message.success("恭喜你,查询成功");

          $router.push({
            name: 'Message', params: {
              poNumber: response.data.poNumber,
              username: response.data.username,
              consignee: response.data.consignee,
              cellphone: response.data.cellphone,
              address: response.data.address,
              orderStatus: response.data.orderStatus,
              carrier:response.data.carrier,
              trackingNo:response.data.trackingNo,
            }
          });
        } else if (response.data.valid === '有效') {
          $message.error("还未使用，请登录使用");//路由跳转到查询的页面
          $router.push('/login');
        } else {
          $message.error("卡号或手机号不正确,请检查");
          console.log(response.data);
        }
      }).catch(error => {
      })
    })


    return {
      loginForm,
      rules,
      menuTab,
      models,
      toggleMenu,
      toGetNum,
      submitForm,
      question1,
      question2,
      login,
      check,
      beforeEnter,
      enter

    }

  }

}
</script>


<style lang="scss" scoped>
#login {
  height: 100vh;
  background-color: #344a5f;
/*  display: flex;
  overflow-x: auto;
  flex: none;*/
}

.login-wrap {
  width: 335px;
  margin: auto;
}

.menu-tab {
  text-align: center;

  li {
    display: inline-block;
    width: 88px;
    line-height: 36px;
    font-size: 14px;
    color: azure;
    border-radius: 2px;
    cursor: pointer;
  }

  .current {
    background-color: #2C3A47;
  }
}


.login-box {
  //margin-top: 5px;
  margin-bottom: 15px;

  label {
    display: block;
    margin-bottom: 3px;
    font-size: 14px;
    color: azure;
  }

  .form-item {
    margin-bottom: 13px;
  }

  .block {
    width: 100%;
    display: block;
  }

  .login-btn {
    margin-top: 15px;
  }
}


</style>
