//提交用户信息/登录
export function myLogin(data) {
    return instance.request({
        method: 'POST',
        url: '/login',
        data: qs.stringify(data)   //post请求需要用qs.stringify()序列化数据 后端可能要加这个注解@RequestParam 是的。
    })
}

