<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  methods:{
    saveState(){
      sessionStorage.setItem('userState', JSON.stringify(this.$store.state.user)) //存储前先用JSON.stringify()方法将json对象转换成字符串形式
    }
  },
  mounted(){
  window.addEventListener('unload',this.saveState)  //当用户离开页面时,会发生 unload 事件
  }

}
</script>


<style>

@import url("./assets/icon/iconfont.css");
html,body,#app{

  background-color: #385166;
  margin: 0;
}
</style>

