<template>
  <transition
      appear
      @before-enter="beforeEnter"
      @enter="enter">
    <div>
      <div style="text-align: right;">

        <el-input type="text" v-model="searchValue" style="width: 200px" @keyup.enter.native="searchAction"
                  placeholder="输入卡号搜索" @input="v=>{searchValue=v.toUpperCase()}"/>

        <el-button size="mini" style="border-radius: 0" icon="el-icon-search" @click="searchAction">搜索</el-button>
        <el-button size="mini" style="border-radius: 0" class="iconfont iconpangxie2" @click="getCard"> 全部
        </el-button>
        <el-button size="mini" style="border-radius: 0" class="iconfont iconpangxie2" @click="fileOutputConfirm4Card">
          导出
        </el-button>
      </div>
      <el-table id="cardTable"
                :data="data.tableData"
                style="width: 100% "
                :header-cell-style="headClass"
                border
                :default-sort="{prop: 'updateTime', order: 'descending'}"
                :cell-style="{padding:'4px'}"
                :row-class-name="tableRowClassName">
        <el-table-column prop="id" label="ID" width="50" v-if="false">
        </el-table-column>
        <el-table-column
            label="序号"
            type="index"
            width="80"
            align="center">
        </el-table-column>
        <el-table-column
            label="创建日期" prop="createTime" width="160">
        </el-table-column>
        <el-table-column
            label="更新日期" prop="updateTime" width="160">
        </el-table-column>
        <el-table-column
            label="商品型号" prop="productType" width="200">
        </el-table-column>
        <el-table-column
            label="卡号" prop="username" width="200">
        </el-table-column>
        <el-table-column v-if="false"
                         label="卡状态" prop="enabled" width="160">
        </el-table-column>
        <el-table-column
            label="使用情况" prop="status" width="100">
        </el-table-column>
        <el-table-column label="卡片状态/卡片操作">
          <template #default="scope">
            <el-button
                size="mini"
                :type="(scope.row.enabled===true)?'primary':'danger'"
                @click="activateCard(scope.row)">
              {{ scope.row.enabled === true ? "已激活" : "激活" }}
            </el-button>
            <el-button
                size="mini"
                type="info" plain
                @click="handleDelete(scope.row)">删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
          background
          layout="prev, pager, next"
          :page-size="pgSize"
          :currentPage="1"
          :total="total"
          @current-change="pageCard">
      </el-pagination>
    </div>
  </transition>
</template>

<script>

import {onMounted, reactive, ref} from "@vue/composition-api";
import {adminGetCard, adminGetCardNextPage, adminGetCardSearch} from "@/api/login";
import {deleteById4card, findById4Card} from "@/api/login";
import gsap from "gsap";
import XLSX from "xlsx";
import {Loading} from "element-ui";

export default {
  name: "Order",
  setup(props, context) {

    const openFullScreen2FileOutput4Card = (() => {
      let loading4Card = Loading.service({
        lock: true,
        text: '数据下载中...',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.8)'
      });
      setTimeout(() => {
        loading4Card.close();
      }, 5000);
    })

    const fileOutputConfirm4Card = (() => {
      $confirm('导出本页文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        exportToExcel4Card();
        openFullScreen2FileOutput4Card();
        setTimeout(() => {
          $message({
            type: 'success',
            message: '导出成功!'
          })
        }, 5100)
      }).catch(() => {
        $message({
          type: 'info',
          message: '已取消'
        });
      });
    })

    const exportToExcel4Card = (() => {
      let table = document.getElementById('cardTable');
      let workBook = XLSX.utils.table_to_book(table);
      try {
        XLSX.writeFile(workBook, 'yourCardFile.xlsx');
      } catch (e) {
        console.log(e);
      }
    })

    const searchValue = ref('')

    const searchAction = (() => {
      let requestData = {
        keyWords: searchValue.value
      }
      if (searchValue.value.trim() === '') {
        $alert('请输入搜索内容');
        pageCard(1);
        return;
      }
      //alert(requestData.keyWords)
      adminGetCardSearch(requestData).then((response) => {
        data.tableData = response.data;
        total.value = response.data.length
        pgSize.value = response.data.length
        //console.log(response.data.length)
        console.log(data.tableData);
      }).catch(error => {
      })
    })

    const data = reactive({
          tableData: [{
            id: '',
            enabled: '',
            status: '',
            createTime: '',
            updateTime: '',
            username: '',
            productType: ''
          }]
        }
    )

    const beforeEnter = ((el) => {
      console.log("beforeEnter-- set initial state 设置初始化状态")
      el.style.transform = 'translateX(100px)'
      el.style.opacity = 0
    })
    const enter = ((el, done) => {
      console.log(" starting to enter-- make transition 开始动画")
      gsap.to(el, {
        duration: 2,
        x: 0,
        opacity: 1,
        onComplete: done,
      })
    })

    const total = ref(0)
    const pgSize = ref(0)

    const pageCard = ((currentPage) => {
      let requestData = {
        currentPage: currentPage,
        pageSize: 50
      }

      adminGetCardNextPage(requestData).then((response) => {
        console.log(response)
        data.tableData = response.data.records;
        total.value = response.data.total;
        pgSize.value = requestData.pageSize;
      }).catch((err) => {
      })

    })

    const getCard = (() => {
      adminGetCard().then((response) => {
        console.log("刷新卡片");
        data.tableData = response.data;
        total.value = response.data.length
        pgSize.value = response.data.length
        console.log(data.tableData);
      }).catch(() => {
      })
    })

    onMounted(() => {
      pageCard(1);

    })

    const tableRowClassName = (({row, rowIndex}) => {
      if (row.enabled === true) {
        return 'warning-row1';
      } else {
        return 'success-row2';
      }
    })


    const {$message} = context.root
    const {$confirm} = context.root
    const {$alert} = context.root
    const {$prompt} = context.root
    const {$notify} = context.root


    const activateCard = ((row) => {
      console.log("卡原状态")
      console.log(row.enabled);
      if (row.enabled === false) {
        findById4Card(row.id)
            .then((response) => {
              if (response.data.enabled === true && response.data.code !== 3001) {
                isActiveNotify()
                pageCard(1)
                console.log(response.data.enabled)
                console.log(response.data)
              } else {
                console.log(response)
                $message.error('权限不足')
              }


            })
            .catch(error => {
              $message.error(error)
            })
      } else {
        isActive()
      }
    })

    const isActiveNotify = (() => {
          $notify({
            title: '恭喜',
            message: '激活成功',
            type: 'success',
            offset: 100
          })
        }
    )


    const isActive = (() => {
          $notify({
            title: '提示',
            message: '卡已激活',
            type: 'success',
            offset: 150
          })
        }
    )


    const handleDelete = ((row) => {
      let cardStatus = row.enabled;
      if (cardStatus) {
        cardStatus = '已激活'
      } else {
        cardStatus = '还未激活'
      }
      $confirm('注意：是否确定删除卡' + '[' + row.username + ']\n' + '此卡现状态为：[' + cardStatus + ']\n' + '确认将清除此卡记录及相关数据。', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        reconfirm(row);
      }).catch(() => {
        $message.info('已取消操作')
      })

    })

    const reconfirm = ((row) => {
      $prompt('请输入[管理员秘钥]', '温馨提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消', type: 'warning'
      })
          .then(({value}) => {
            if (value === '123') {
              deleteById4card(row.id).then((response) => {
                if (response.data.code !== 2001 && response.data.code !== 3001) {
                  console.log(response)
                  $alert('卡删除成功', '删除卡片', {
                    confirmButtonText: '好的',
                    callback: pageCard(1)
                  })
                } else {
                  console.log(response)
                  $message.error('权限不足')
                }
              })
            } else {
              $message.error('输入不正确,已取消操作')
            }
          })
          .catch(() => {
            $message.success('已取消操作')
          })

    })

    const headClass = (() => {
      return 'text-align:left;background:#313C50;'
    })


    return {
      data,
      activateCard,
      isActive,
      isActiveNotify,
      handleDelete,
      getCard,
      tableRowClassName,
      reconfirm,
      headClass,
      beforeEnter,
      enter,
      pageCard,
      total,
      searchAction,
      searchValue,
      pgSize,
      openFullScreen2FileOutput4Card,
      fileOutputConfirm4Card,
      exportToExcel4Card
    }

  }
}

</script>

<style lang="scss">

div > .el-input > input {
  height: 27px;
  border-radius: 0;
}


.el-table .warning-row1 {
  background: #7188b0;
  color: #0f0101;

}


.el-table .success-row2 {
  background: #8da0ae;
  color: #4d4545;

}


</style>

